<template>
    <div class="song-list">
      <ul>
        <li v-for="(song,index) in songs" :key="song.id" class="item" @click="selectItem(song,index)">
          <div class="rank" v-show="isRank">
            <span :class="getRankClass(index)">{{getRankText(index)}}</span>
          </div>
          <div class="content">
            <div class="name">{{song.name}}</div>
            <div class="desc">{{getDesc(song)}}</div>
          </div>
        </li>
      </ul>
    </div>
</template>
<script>
export default {
  props: {
    songs: {
      type: Array,
      default() {
        return [];
      }
    },
    isRank: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    getDesc(song) {
      return `${song.singer}-${song.album}`;
    },
    selectItem(song, index) {
      this.$emit("select", song, index);
    },
    getRankClass(index) {
      if (index < 3) {
        return `icon icon${index}`;
      }
    },
    getRankText(index) {
      if (index > 2) {
        return index;
      }
    }
  }
};
</script>
<style lang="stylus" scoped>
@import '~common/stylus/variable';
@import '~common/stylus/mixin';

.song-list {
  .item {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    height: 64px;
    font-size: $font-size-medium;

    .rank {
      flex: 0 0 25px;
      width: 25px;
      margin-right: 30px;
      text-align: center;

      .icon {
        display: inline-block;
        width: 25px;
        height: 24px;
        background-size: 25px 24px;

        &.icon0 {
          bg-image('first');
        }

        &.icon1 {
          bg-image('second');
        }

        &.icon2 {
          bg-image('third');
        }
      }

      .text {
        color: $color-theme;
        font-size: $font-size-large;
      }
    }

    .content {
      flex: 1;
      line-height: 20px;
      overflow: hidden;

      .name {
        no-wrap();
        color: $color-text;
      }

      .desc {
        no-wrap();
        margin-top: 4px;
        color: $color-text-d;
      }
    }
  }
}
</style>



